import { __decorate } from "tslib";
import { Component, Vue, Prop } from 'vue-property-decorator';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        this.index = 0;
        this.businessId = '';
        this.items = [
            {
                "name": "分类",
                "level": 1,
                "id": "0972f3f0-c8d1-11ea-911d-d9230b308413"
            }
        ];
    }
    mounted() {
        this.$store.commit('SET_SESSION_ID', 'noSession');
        this.setBusiness();
        this.getCommodityClassList();
    }
    boxClick(index) {
        this.index = index;
        let backData = this.items[this.index];
        this.$emit('change', backData);
    }
    setBusiness() {
        let businessId = this.$route.query.state.split('@')[1];
        this.$store.commit('SET_BUSINESS_ID', businessId);
        this.businessId = businessId;
    }
    getCommodityClassList() {
        const toast = this['$createToast']({
            time: 0,
            txt: '数据获取中...'
        });
        toast.show();
        this.$store.dispatch('getCommodityClassList', { business: this.businessId }).then((response) => {
            if (response.success) {
                let list = response.data.list;
                this.$set(this, 'items', list);
                this.$emit('businissChange', response.data.businessInfo);
                this.boxClick(0);
            }
            toast.hide();
        });
    }
};
__decorate([
    Prop()
], Home.prototype, "tabNumObj", void 0);
Home = __decorate([
    Component({
        components: {},
        filters: {}
    })
], Home);
export default Home;
