import { __decorate } from "tslib";
import { Component, Vue } from 'vue-property-decorator';
import banner from '@/components/common-components/bannerSlide.vue';
import payBox from '@/components/common-components/orderPayment.vue';
import imageUpload from '@/components/common-components/image-upload.vue';
import selectTab from './selectTab.vue';
import businessinfo from './businessInfo.vue';
import wx from 'weixin-js-sdk';
let Home = class Home extends Vue {
    constructor() {
        super(...arguments);
        this.items = [];
        this.pullDownRefresh = false;
        this.pullDownRefreshThreshold = 60;
        this.pullDownRefreshStop = 40;
        this.pullDownRefreshTxt = 'Refresh success';
        this.pullUpLoad = false;
        this.pullUpLoadThreshold = 0;
        this.pullUpLoadMoreTxt = 'Load more';
        this.pullUpLoadNoMoreTxt = 'No more data';
        this.customPullDown = false;
        // 商家信息
        this.businessInfoo = {};
        // 当前分类id
        this.classId = '';
        this.className = '';
    }
    get classCommodity() {
        return this.$store.state.classCommodity;
    }
    // 当前分类数量
    get selectNumClass() {
        return this.$store.state.selectNumClass;
    }
    get options() {
        return {
            pullDownRefresh: this.pullDownRefreshObj,
            pullUpLoad: this.pullUpLoadObj,
            scrollbar: false
        };
    }
    get pullDownRefreshObj() {
        let treshold = parseInt(this.pullDownRefreshThreshold);
        return this.pullDownRefresh ? {
            threshold: treshold,
            // Do not need to set stop value, but you can if you want
            // stop: parseInt(this.pullDownRefreshStop),
            txt: this.pullDownRefreshTxt
        } : false;
    }
    onPullingDown() {
    }
    onPullingUp() {
    }
    foodAdd(food, index) {
        let num = food.num || 0;
        this.$set(this.items[index], 'num', num + 1);
        this.$store.commit('SET_SELECT_COMMODITY_LIST', { type: 'add', item: this.items[index] });
        this.updateClassNum('add');
    }
    foodReduce(food, index) {
        let num = food.num || 0;
        let setNum = food.num - 1;
        setNum <= 0 ? 0 : setNum;
        this.$set(this.items[index], 'num', setNum);
        this.$store.commit('SET_SELECT_COMMODITY_LIST', { type: 'reduce', item: this.items[index] });
        this.updateClassNum('reduce');
    }
    updateClassNum(type) {
        this.$store.commit('SET_SELECT_COMMODITY_NUM', { classId: this.classId, type: type });
    }
    tabChange(data) {
        this.classId = data.id;
        this.$set(this, 'className', data.name);
        this.getCommodityListByClass(data);
    }
    getCommodityListByClass(classData) {
        let classId = classData.id;
        const toast = this['$createToast']({
            time: 0,
            txt: '商品获取中...'
        });
        if (this.classCommodity[classId] && this.classCommodity[classId].length > 0) {
            this.$set(this, 'items', this.classCommodity[classId]);
        }
        else {
            this.$store.dispatch('getCommodityListByClass', { class: classId }).then((response) => {
                if (response.success) {
                    let list = response.data.list;
                    this.$set(this, 'items', list);
                    this.$store.commit('SET_CLASS_COMMODITY', { classId: classId, list: list });
                }
                toast.hide();
            });
        }
    }
    getBusinissInfo(info) {
        document.title = info.name;
        this.$set(this, 'businessInfoo', info);
    }
    commodityShow(item, index) {
        this.$createDialog({
            type: 'confirm',
            confirmBtn: {
                text: '添加',
                active: true
            },
            cancelBtn: {
                text: '关闭',
                active: false,
                disabled: false,
                href: 'javascript:;'
            },
            onConfirm: () => {
                this.foodAdd(item, index);
            }
        }, (createElement) => {
            return [
                createElement('div', {
                    'class': {
                        'my-title': true
                    },
                    slot: 'title'
                }, [
                    createElement('img', {
                        attrs: {
                            src: item.imgUrlList[0] + '?w=200'
                        }
                    }),
                    createElement('p', item.name)
                ]),
                createElement('p', {
                    'class': {
                        'my-content': true
                    },
                    slot: 'content'
                }, '价格:' + item.price + '元/' + item.unit)
            ];
        }).show();
    }
    shareClick() {
        let str = `type=commodity&commodityList=[{"id":"bc8f37d0-fb41-11ea-8a76-532bd2785fca","name":"巨峰葡萄","num":1,"price":"0.1","imgUrl":"http://img.beijixiong.club:4869/931477297cfd002e44a3906cc8bc2e9c"},{"id":"14067010-0244-11eb-a74a-f3981c383190","name":"哈密瓜","num":1,"price":"0.01","imgUrl":"http://img.beijixiong.club:4869/2a4d2566daa8c8bd5a80ce22eafbb447"}]&fee=0.11&businessId=8a146790-f97e-11ea-8165-3111824eebe8`;
        wx.miniProgram.navigateTo({
            url: '/pages/jumbprocess/index?' + str
        });
        // wx.ready(function () {      //需在用户可能点击分享按钮前就先调用
        //   wx.onMenuShareAppMessage({
        //         title : title, // 分享标题
        //         desc : '海尔定制母婴家电，让你魔法加身，搞定养娃的大问题！', // 分享描述
        //         link : myurl, // 分享链接
        //         imgUrl : 'http://diy-haier.highset.cn/fx.jpg', // 分享图标
        //         type : 'link', // 分享类型,music、video或link，不填默认为link
        //         success : function() {
        //             // 用户确认分享后执行的回调函数
        //         },
        //         cancel : function() {
        //             // 用户取消分享后执行的回调函数
        //         }
        //     })
        // });
    }
};
Home = __decorate([
    Component({
        components: {
            banner,
            payBox,
            selectTab,
            imageUpload,
            businessinfo
        },
        filters: {
            getFormatDate(date) {
                return new Date(date).getFullYear() + '年' + (new Date(date).getMonth() + 1) + '月' + new Date(date).getDate() + '号';
            },
            getFoodNum(food) {
                return (food.num && food.num > 0) ? food.num : 0;
            }
        }
    })
], Home);
export default Home;
