<template>
  <transition name="cube-action-sheet-fade">
    <cube-popup
      type="action-sheet"
      :class="{'cube-action-sheet_picker': pickerStyle}"
      :center="false"
      :mask="true"
      :z-index="zIndex"
      v-show="isVisible"
      @mask-click="maskClick">
      <transition name="cube-action-sheet-move">
        <div class="payshow-commodityList" v-show="isVisible">
           <selectCommodity :isShow='isVisible' :isAtOrder='false'></selectCommodity>
        </div>
      </transition>
    </cube-popup>
  </transition>
</template>
<script type="text/ecmascript-6">
  import selectCommodity from '@/components/common-components/selectCommodity'
  const COMPONENT_NAME = 'cube-action-sheet'
  const EVENT_SELECT = 'select'
  const EVENT_CANCEL = 'cancel'
  export default {
    name: COMPONENT_NAME,
    data () {
      return {
        zIndex: 50,
        isVisible: false
      }
    },
    props: {
      pickerStyle: {
        type: Boolean,
        default: false
      },
      visible: {
        type: Boolean,
        default: true
      }
    },
    computed: {
      selectCommodityList () {
        return this.$store.state.selectCommodityList
      }
    },
    mounted () {
      this.$watch('visible', (newVal, oldVal) => {
        if (newVal) {
          this.show()
        } else if (oldVal && !this._createAPI_reuse) {
          this.hide()
        }
      }, {
        immediate: true
      })
    },
    methods: {
      maskClick() {
        this.cancel()
      },
      cancel() {
        this.$emit(EVENT_CANCEL)
      },
      itemClick(item, index) {
        this.hide()
        this.$emit(EVENT_SELECT, item, index)
      },
      show() {
        this.isVisible = true
      },
      hide() {
        this.isVisible = false
      }
    },
    watch: {
      selectCommodityList (arr) {
        if (arr.length <= 0) {
          this.cancel()
        }
      }
    },
    components: {
      selectCommodity
    }
  }
</script>
<style lang="stylus" rel="stylesheet/stylus">
  .payshow-commodityList
    background white
    margin 0 auto
</style>
<style lang="stylus" rel="stylesheet/stylus">
  .cube-action-sheet-fade-enter, .cube-action-sheet-fade-leave-active
    opacity: 0
  .cube-action-sheet-fade-enter-active, .cube-action-sheet-fade-leave-active
    transition: all .2s ease-in-out
  .cube-action-sheet-panel
    text-align: center
    background-color: red
  .cube-action-sheet-move-enter, .cube-action-sheet-move-leave-active
    transform: translate3d(0, 100%, 0)
  .cube-action-sheet-move-enter-active, .cube-action-sheet-move-leave-active
    transition: all .1s ease-in-out
  .cube-action-sheet-cancel
    background-color: red
  .cube-action-sheet-cancel span, .cube-action-sheet-title, .cube-action-sheet-item
    display: block
    padding: 17px 16px
    margin: 0
    text-align: center
    overflow: hidden
    white-space: nowrap
    font-weight: normal
    line-height: 1
    color: black
    background-color: white
  .cube-action-sheet-cancel span, .cube-action-sheet-item
    &:active
      background-color: yellow
  .cube-action-sheet-title
    padding-top: 16px
    padding-bottom: 16px
    color: yellow
  .cube-action-sheet-content
    overflow: hidden
    background: yellow
  .cube-action-sheet-list
    list-style: none
  .cube-action-sheet-item
    list-style: none
    user-select: none
    &:last-of-type
      border-none()
    &[data-align="left"]
      text-align: left
    &[data-align="right"]
      text-align: right
  .cube-action-sheet-space
    height: 6px
    background-color: yellow
  .cube-action-sheet-item_active
    color: yellow
  .cube-action-sheet_picker
    .cube-action-sheet-space
      height: 0
    .cube-action-sheet-title
      height: 1em
      padding-top: 21px
      padding-bottom: 21px
</style>